import * as React from 'react'
import styled from '../styles/styled-components'
import { Container } from './Container'

const InnerFooter = styled(Container)`
  padding: ${p => p.theme.dimensions.footerPadding}rem
    ${p => p.theme.dimensions.containerPadding}rem;
  font-size: 1.125rem;

  ${p => p.theme.media.lg`
    padding: ${t => t.theme.dimensions.footerPadding}rem
      ${t => t.theme.dimensions.containerPadding * 3}rem;
  `};
`

export const Footer: React.SFC<{}> = () => (
  <footer>
    <InnerFooter>{' 20122018, Makeomatic'}</InnerFooter>
  </footer>
)



