import * as React from 'react'
import styled from '../styles/styled-components'
import { ReactComponent as LogoIcon } from '../assets/svg/logo.svg'
import Link from 'gatsby-link'

import { onEvent } from '../styles/mixins'
import { Container } from './Container'

const StyledHeader = styled.header`
  height: 136px;
  color: ${p => p.theme.colors.white};
`

const HeaderInner = styled(Container)`
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%;
  padding: ${p => p.theme.dimensions.containerPadding}rem;

  ${p => p.theme.media.lg`
    padding-left: ${t => t.theme.dimensions.containerPadding * 3}rem;
    padding-right: ${t => t.theme.dimensions.containerPadding * 3}rem;
  `};
`

const HeaderLink = styled(Link)`
  color: ${p => p.theme.colors.white};

  ${onEvent`
    text-decoration: none;
  `};
`

const LogoContainer = styled.div`
  flex-grow: 1;
  font-size: 0;
`

const Nav = styled.nav`
  display: flex;
  flex-flow: row nowrap;
  font-size: 1.125rem;
`

const NavLink = styled(HeaderLink).attrs({
  activeClassName: 'active',
  exact: true,
})`
  margin-right: 2rem;
  text-transform: uppercase;

  &:last-child {
    margin-right: 0;
  }

  &.active {
    font-weight: 700;
  }
`

interface HeaderProps {
  title: string
}

export const Header: React.SFC<HeaderProps> = () => (
  <StyledHeader>
    <HeaderInner>
      <LogoContainer>
        <HeaderLink to="/" href="/">
          <LogoIcon />
        </HeaderLink>
      </LogoContainer>
      <Nav>
        <NavLink to="/" href="/">
          Work
        </NavLink>
        <NavLink to="/team" href="/team">
          About
        </NavLink>
        <NavLink to="/process" href="/process">
          Our process
        </NavLink>
        <NavLink to="/contacts" href="/contacts">
          Get in touch
        </NavLink>
      </Nav>
    </HeaderInner>
  </StyledHeader>
)



