import * as React from 'react'
import styled from '../styles/styled-components'
import GatsbyLink from 'gatsby-link'
import { Container } from './Container'
import { ReactComponent as LogoIcon } from '../assets/svg/logo.svg'
import { ReactComponent as GithubIcon } from '../assets/svg/icon_github.svg'
import { ReactComponent as BehanceIcon } from '../assets/svg/icon_behance.svg'

const StyledContactsContainer = styled.section`
  background-color: ${p => p.theme.colors.white};
  color: ${p => p.theme.colors.black};
`

const StyledContacts = styled(Container)`
  padding: ${p => p.theme.dimensions.containerPadding * 1.5}rem
    ${p => p.theme.dimensions.containerPadding}rem;

  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.125rem;
  font-weight: 500;

  a {
    color: ${p => p.theme.colors.black};
  }

  ${p => p.theme.media.lg`
    padding: ${t => t.theme.dimensions.containerPadding * 1.5}rem
      ${t => t.theme.dimensions.containerPadding * 1.5}rem
      ${t => t.theme.dimensions.containerPadding * 1.5}rem
      ${t => t.theme.dimensions.containerPadding * 3}rem;
  `};
`

const StyledLogoIcon = styled(LogoIcon)`
  color: ${p => p.theme.colors.black};
`

const Address = styled.address`
  font-style: normal;

  p:last-child {
    margin-bottom: 0;
  }
`

const Links = styled.div`
  font-weight: 600;
  text-transform: uppercase;
`

const StyledLink = styled(GatsbyLink)`
  display: block;
`

const Social = styled.div`
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
`

const Github = styled(GithubIcon)`
  width: 1.2rem;
  height: 1.2rem;
`

const Behance = styled(BehanceIcon)`
  width: 1.2rem;
  height: 1.2rem;
  margin-top: 1.5rem;
`

export const Contacts: React.SFC<{}> = () => (
  <StyledContactsContainer>
    <StyledContacts>
      <StyledLogoIcon />
      <Address>
        <p>
          +1 (236) 777 41 46
          <br />
          info@makeomatic.ca
        </p>
        <p>
          602 Citadel Parade,
          <br />
          Vancouver, BC Canada
        </p>
      </Address>
      <Links>
        <StyledLink to="/">Work</StyledLink>
        <StyledLink to="/team">About</StyledLink>
        <StyledLink to="/process">Our process</StyledLink>
        <StyledLink to="/contacts">Get in touch</StyledLink>
      </Links>
      <Social>
        <a href="https://github.com/makeomatic" rel="noopener noreferrer" target="github">
          <Github />
        </a>
        <a href="https://www.behance.net/talamasca" rel="noopener noreferrer" target="behance">
          <Behance />
        </a>
      </Social>
    </StyledContacts>
  </StyledContactsContainer>
)



